import {
  ADD_PRODUCT,
  GET_PRODUCT,
  REMOVE_PRODUCT,
  FILTER_PRODUCT,
} from "../types/Actiontype";
import data from "../../data/MOCK_DATA.json";

const initialState = {
  data: data,
  count: 0,
  card_product: [],
  search_p: "",
};
const reducer = (state = initialState, action) => {
  switch (action.type) {
    case GET_PRODUCT:
      return {
        ...state,
        data: state.data,
      };

    case REMOVE_PRODUCT:
      const filter = state.card_product.filter(
        (item) => item.pid !== action.pid
      );
      return {
        ...state,
        card_product: filter,
        count: state.count - 1,
      };

    case ADD_PRODUCT:
      const newobject = {
        ...action.payload,
        pid: state.count + 2,
        qnt: action.qnt,
      };

      return {
        ...state,

        card_product: [...state.card_product, newobject],
        count: state.count + 1,
      };

    case FILTER_PRODUCT:
      const lowercase = action.payload.toLowerCase();

      const search_product = state.data.filter((item) =>
        item.product_name.toLowerCase().includes(lowercase)
      );

      return {
        ...state,
        data: search_product,
        search_p: search_product.length,
      };

    default:
      return state;
  }
};

export default reducer;
